import React from "react";
import styles from "../css/MyPage.module.css";
import { Link } from 'react-router-dom';

export default function MyPage() {
  return (
    <div class={styles.row}>
        <div class="column left" id="cl">
            <div class = "profile">
                
                <div></div>
            </div>
            <button type="button" class="black">  </button> <br></br>
            <button type="button" class="black">  </button>
        </div>
        <div class="column right" id="cr">
          <form action="/action_page.php">
            <label for="id"> ID </label>
            <input type="text" id ="id" value="ewhayeon"/><br></br>

            <label for="nickname"> </label>
            <input type="text" id ="nickname" value=""/><br></br>

            <label for="phonenum">  </label>
            <input type="tel" id="phonenum" name="phone" pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}" required/>
            <button type="submit"  class="green"> </button><br/>

            <label for="PWbefore"> PW </label>
            <input type="password" id="PWbefore"/><br/>

            <label for="PWafter"> PW </label>
            <input type="password" id="PWafter"/>
            <button type="submit" class="green"> </button><br/><br/><br/>
            <div>*  ,   10~15 </div>
            <button type="submit" class="black"> </button>
          </form>
        </div>
      </div>
  );
}

