import React from 'react';
import { Link } from 'react-router-dom'; //  

import styles from '../css/HomePage.module.css';

import { useState } from 'react';

import sample from "../img/sample.PNG";
import samplelocation from "../img/location.png";
import NavBar from '../component/NavBar';

<link rel="stylesheet" type="text/css" href="https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css"></link>

// HOT 
//   
function Section() {
  return <section class={styles.section}>
    <div className={styles.green}>
      <div className={styles.hottitle}>
        {/* <h2>HOT TOPIC</h2> */}
        <div className={styles['link-1']}>
          <a className={styles.hottopic} href="#">
            <span className={styles.thick}>HOT </span><span class={styles.thin}>TOPIC</span>
          </a>
          <p className={styles.homep}>  </p>
        </div>
      </div>
      <div className={styles.hotlist_container}>
        <Link to="/StoreDetail" class={styles.hotlist}>
          <img src={sample} width="250" height="260" />
          <div class={styles.hotdesc}>
            <div class={styles.upcontainer}>
              <div class={styles.descname}> </div>
              {/* <div class = "star"></div> */}
            </div>
            <div class={styles.descloca}><img src={samplelocation} width="14" height="14" />  </div>
            <div class={styles.menuname}> </div>
            <div class={styles.review}>  .</div>
          </div>
        </Link>
        <a href="#" class={styles.hotlist}>
          <img src={sample} width="250" height="260" />
          <div className={styles.hotdesc}>
            <div className={styles.upcontainer}>
              <div className={styles.descname}> </div>
              {/* <div class = "star"></div> */}
            </div>
            <div className={styles.descloca}><img src={samplelocation} width="14" height="14" />  </div>
            <div className={styles.menuname}> </div>
            <div className={styles.review}>  .</div>
          </div>
        </a>
        <a href="#" className={styles.hotlist}>
          <img src={sample} width="250" height="260" />
          <div className={styles.hotdesc}>
            <div className={styles.upcontainer}>
              <div className={styles.descname}> </div>
              {/* <div class = "star"></div> */}
            </div>
            <div className={styles.descloca}><img src={samplelocation} width="14" height="14" />  </div>
            <div className={styles.menuname}> </div>
            <div className={styles.review}>  .</div>
          </div>
        </a>
        <a href="#" class={styles.hotlist}>
          <img src={sample} width="250" height="260" />
          <div class={styles.hotdesc}>
            <div class={styles.upcontainer}>
              <div class={styles.descname}> </div>
              {/* <div class = "star"></div> */}
            </div>
            <div class={styles.descloca}><img src={samplelocation} width="14" height="14" />  </div>
            <div class={styles.menuname}> </div>
            <div class={styles.review}>  .</div>
          </div>
        </a>
      </div>
    </div>
  </section>
}

function Homepage() {
  return (
    <div className="">
      <Section></Section>
      <div class={styles.Hbox}>
        <Link to="/StoreListView" class={styles.homeButton}> </Link>
        <Link to="/CreateStore" class={styles.homeButton}>  </Link>
        <Link to="/reviewAll" class={styles.homeButton}> </Link>
        {/*     */}
      </div>
    </div>
  );
}

export default Homepage;
