  // MinusButton.addEventListener("click", removeNutrient(nutrient));
  // nutrient  key     
  const [menudata, setData] = useState()
  var menuarr = new Array();

  useEffect(() => {
    fetch(`/CreateMenu_send_data/${params}`, { //json  
      headers: {
        Accept: 'application/json',
        method: 'GET'
      }
    })
      .then(response => response.json())
      .then(jsonData => {
        var length = Object.keys(jsonData).length
        for (var i = 0; i < length; i++) { //   
          menuarr[i] = Object.values(jsonData)[i]
          // menuarr[i]["key"]=Object.keys(jsonData)[i] //  
        }
        setData(menuarr)
      })
      .catch(
        (err) => console.log(err))
  }, [])
  console.log("menuarr")
  console.log(menuarr)

  // if (Return_value == 1) {  //Return_value     1     on submit  
